li{
  list-style: none;
}
body{
  background-color: rgba(36, 35, 35, 1);
}

/* App.js의 css */



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




/* Nav.js의 css */

.nav{
  
}
.nav-trans{
  position: absolute;
  top: 50%;
  transform: rotateZ(-90deg);
  transform-origin: 0 50%;
  
}
.nav-left h3,.nav-right h3{
  color: #ffffff;
  font-size: 15px;
}

.nav-trans-right{
  position: absolute;
  top: 50%;
  transform: rotateZ(-90deg);
  transform-origin: 50% 0;
}

.nav-left{
  left: 40px;
  position: fixed;
}
.nav-right{
  right: -40px;
  position: fixed;
}


/* Wrapper.js의 css */

.top_home{
 background-image: url('./Banner.png'); 
 background-repeat: no-repeat;
 background-position: center;
 background-size: cover;
 height: 100vh;
}

.About{
  height: 100vh;
    width: 100vw;
    text-align: center;
    vertical-align: middle;
    display: table-cell;
}
.my_info{
  width: 50vw;
    height: 47vh;
    background-color: rgb(4, 2, 29);
    background-image: url('./Nwood.jpg');
    display: inline-block;
    position: relative;
}
.my_info::after{
  content: "";
  display: block;
  clear: both;
}

.my_info>img{
  width: 25%;
  max-width: 280px;
  padding: 35px 35px 0 20px;
}
.my_info ul, .my_info>img{
  float: left;
}
.my_info ul{
  color: #ffffff;
  padding: 35px 0 0 40px;
  width: 35%;

}
.my_info ul li{
  font-size: 22px;
  margin-top: 16px;
}

.skill{
  background-size: cover;
  
 
}
.skill{
  width: 3vw;
  position: absolute;
  right: 0;
  bottom: 0;
  color: #ffffff;
  padding: 20px;
  text-align: center; margin-top: 20px;
  cursor: pointer;
}

.skill img{
  width: 100%;
  animation: motion 0.3s linear 0s infinite alternate; margin-top: 0;
}
@keyframes motion{
0% {margin-bottom: 0px;}
100% {margin-bottom: 10px;}
}


